@mixin backgroundRed($name, $ifRedColour, $elseColour) {
  @if $name == red {
    background-color: $ifRedColour; }
  @else {
    background-color: $elseColour; } }

@mixin colorRed($name, $ifRedColour, $elseColour) {
  @if $name == red {
    color: $ifRedColour; }
  @else {
    color: $elseColour; } }


