//TODO rule az tulajdonság szeretne lenni
@mixin ifDark($name, $rule, $ifDark, $ifNotDark) {
  @if $name == dark {
    #{$rule}: $ifDark; }
  @else {
    #{$rule}: $ifNotDark; } }

@mixin boxShadowIfDark($name, $ifDark, $ifNotDark) {
  @include ifDark($name, box-shadow, $ifDark, $ifNotDark); }

@mixin bgImageIfDark($name, $ifDark, $ifNotDark) {
  @include ifDark($name, background-image, $ifDark, $ifNotDark); }

@mixin colorIfDark($name, $ifDark, $ifNotDark) {
  @include ifDark($name, color, $ifDark, $ifNotDark); }
